:root {
  --bg: #111;
  --text: #fff;
  --accent: #ffcc33;
  --container-max: 1500px;
}

body {
  margin: 0;
  font-family: 'Pretendard', sans-serif;
  background: var(--bg);
  color: var(--text);
  font-family: "The Jamsil", sans-serif;
}

section {
  width: 100%;
  scroll-margin-top: 100px;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

/* Header */
.header {
  position: sticky;
  top: 0;
  background: #000;
  padding: 20px 0;
  z-index: 1000;
  font-family: "Cafe24Supermagic", sans-serif;
  font-weight: 400;
}

.nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.nav-links {
  display: flex;
  gap: 60px;
  margin-right: 60px;
  list-style: none;
}

.nav-links a {
  color: var(--text);
  text-decoration: none;
  font-size: 20px;
  transition: 0.3s;
  font-size: 1.5rem;
}

.nav-links a:hover {
  color: var(--accent);
}


/* „Ö°„Ö°„Ö°„Ö°„Ö°Home „Ö°„Ö°„Ö°„Ö°„Ö° */
.hero {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  position: relative;
  overflow: visible;
  background: linear-gradient(135deg, #111, #111, #111);
  background-size: 400% 400%;
  animation: gradientShift 12s ease infinite;
}

@keyframes gradientShift {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

.hero-wrapper {
  text-align: center;
  position: relative;
  overflow: visible;
}

.hero-text {
  position: absolute;
  top: -55px;
  left: 38%;
  transform: translateX(-50%) rotate(6deg);
  font-family: "Cafe24Supermagic", sans-serif;
  font-size: 1.7rem;
  font-weight: 500;
  color: #fff;
  z-index: 10;
  white-space: nowrap;
  letter-spacing: 0.5px;
  overflow: hidden;
}

.hero-text .highlight {
  color: #ffde59;
  font-weight: 600;
}

.cursor {
  display: inline-block;
  width: 3px;
  height: 1em;
  background: #fff;
  margin-left: 2px;
  animation: blink 0.8s step-end infinite;
}

@keyframes blink {
  50% {
    opacity: 0;
  }
}

.hero-main {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 40px;
  overflow: visible;
}

.hero-j,
.hero-y {
  filter: drop-shadow(0 4px 12px rgba(0, 0, 0, 0.4));
}

.hero-j {
  position: relative;
  left: 130px;
  top: 20px;
  width: 480px;
  z-index: 5;
}

.hero-y {
  width: 320px;
  position: relative;
  right: 110px;
  bottom: 160px;
  z-index: 2;
}

.hero-j-floating,
.hero-y-floating {
  pointer-events: none;
}

.hero-photo {
  position: relative;
  top: -40px;
  right: 20px;
  width: 420px;
  height: 490px;
  transform: rotate(-2deg);
  z-index: 3;
  filter: drop-shadow(0 4px 16px rgba(0, 0, 0, 0.5));
}

.profile-bg {
  width: 100%;
  height: 100%;
  display: block;
}

.jyphoto {
  position: absolute;
  top: 30px;
  left: 51%;
  transform: translateX(-50%);
  width: 360px;
  border-radius: 6px;
}

.name {
  position: absolute;
  bottom: 5px;
  left: 65%;
  transform: translateX(-50%) rotate(8deg);
  color: #000;
  font-family: "Cafe24Supermagic", sans-serif;
  font-weight: 700;
  font-size: 2.5rem;
}





/* „Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°About„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö° */
.about {
  color: #fff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.about-title {
  position: relative;
  left: 50px;
  top: 20px;
}

.about-title-img {
  width: 500px;
  transform: rotate(-4deg);
  display: block;
}

/* üîπ Î©îÏù∏ 3Ïó¥ Î†àÏù¥ÏïÑÏõÉ */
.about-content {
  display: grid;
  grid-template-columns: 1fr 1.2fr 1.1fr;
  gap: 60px;
  align-items: start;
  justify-content: center;
}

/* ÏôºÏ™Ω ÌîÑÎ°úÌïÑ */
.about-left {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  position: relative;
  top: 70px;
}

.photo-wrap {
  position: relative;
  width: 260px;
}

.masking {
  position: absolute;
  top: -50px;
  left: 80px;
  width: 100px;
  transform: rotate(-4deg);
  z-index: 2;
}

.photo-frame {
  background: #fff;
  padding: 15px;
  transform: rotate(-8deg);
  position: relative;
  left: 20px;
}

.photo-main {
  width: 200px;
  display: block;
}

.photo-person {
  position: absolute;
  top: 25px;
  left: 50%;
  transform: translateX(-50%) rotate(6deg);
  width: 250px;
}


.photo-name {
  color: #000;
  text-align: center;
  font-family: "Cafe24Supermagic", sans-serif;
  font-weight: 700;
  margin-top: 20px;
  font-size: 1.8rem;
}

.photo-name span {
  font-weight: 400;
  font-size: 20px;
}

.about-info {
  font-size: 15px;
  font-weight: 300;
  line-height: 1.6;
  margin-top: -15px;
}

/* Ï§ëÏïô ÏΩòÌÖêÏ∏† */
.about-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 30px;
  position: relative;
  top: 50px;
}

.about-section h3 {
  font-family: "Cafe24Supermagic", sans-serif;
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 10px;
  border-bottom: 1px solid #444;
  padding-bottom: 5px;
}

.about-section ul {
  list-style: none;
  padding: 0;
  margin: 0;
  color: #ddd;
  line-height: 1.8;
  font-weight: 300;
}

/* „Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö° Ïò§Î•∏Ï™Ω Ïä§ÌÇ¨ „Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°*/

.about-right {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-right: 40px;
  margin-top: -65px;
}

.about-right h3 {
  border-bottom: 1px solid #444;
  padding-bottom: 5px;
  font-family: "Cafe24Supermagic", sans-serif;
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 20px;
}

/* Ïä§ÌÇ¨ Î¶¨Ïä§Ìä∏ Í∏∞Î≥∏ ÏÑ∏ÌåÖ */
.skill-list {
  font-weight: 300;
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.skill-item {
  display: flex;
  align-items: center;
  gap: 20px;
}

/* ÏôºÏ™Ω ÏïÑÏù¥ÏΩò + Ïù¥Î¶Ñ */
.skill-left {
  display: flex;
  align-items: center;
  gap: 10px;
  min-width: 130px;
  /* Ïù¥Î¶Ñ Ìè≠ ÌôïÎ≥¥ */
}

.skill-icon {
  width: 25px;
  height: 25px;
}

.skill-name {
  font-size: 14px;
  color: #fff;
  font-weight: 300;
}

/* Ïò§Î•∏Ï™Ω: Î∞î + Ïà´Ïûê */
.skill-right {
  display: flex;
  align-items: center;
  gap: 10px;
  flex: 1;
  /* Ïò§Î•∏Ï™Ω Ï†ÑÏ≤¥ Í≥µÍ∞Ñ Ï∞®ÏßÄ */
}

/* Ïä§ÌÇ¨ Î∞î */
.bar {
  flex: 1;
  height: 13px;
  background: #333;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
}

/* Ï±ÑÏõåÏßÄÎäî Î∂ÄÎ∂Ñ */
.fill {
  height: 100%;
  background: linear-gradient(90deg, #ffcc33, #fff176);
  border-radius: 10px;
  width: 0%;
  transition: width 1.6s ease-in-out;
}


.fill.active {
  width: var(--target-width);
}

/* Ïò§Î•∏Ï™Ω Ïà´Ïûê */
.skill-num {
  font-size: 14px;
  color: #fff;
  min-width: 45px;
  text-align: right;
  white-space: nowrap;
  letter-spacing: 0.3px;
}

/* „Ö°„Ö°„Ö° Î∞òÏùëÌòï „Ö°„Ö°„Ö° */
@media (max-width: 900px) {
  .skill-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }

  .skill-left {
    min-width: auto;
  }

  .skill-right {
    width: 100%;
  }

  .skill-num {
    text-align: left;
  }
}




/* „Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°Works „Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°*/
/* Ï†ÑÏ≤¥ ÏÑπÏÖò */
.works-section {
  color: #fff;
  text-align: center;
  padding: 30px 0;
}

/* ÏÉÅÎã® WORKS Ïä§Ìã∞Ïª§ */
.works-title {
  margin-bottom: 30px;
}

.works-logo {
  width: 350px;
  display: inline-block;
}

/* ÌïÑÌÑ∞ Î≤ÑÌäº */
.filter-buttons {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 50px;
}

.filter-buttons button {
  background: #fff;
  border: none;
  border-radius: 50px;
  padding: 10px 28px;
  font-size: 1.1rem;
  cursor: pointer;
  transition: 0.3s;
  color: #000;
  font-family: 'TheJamsil', 'Cafe24Supermagic', sans-serif;
  font-weight: 500;
  /* ÌïÑÏöî Ïãú Ï°∞Ï†ï */
}

.filter-buttons .active {
  background: #ffce3a;
  font-weight: 600;
}

/* Ïä¨ÎùºÏù¥Îìú Î†àÏù¥ÏïÑÏõÉ */
.work-display {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 25px;
  position: relative;
  top: -15px;
}

.arrow {
  font-size: 40px;
  color: #00d4d4;
  cursor: pointer;
  user-select: none;
  transition: 0.3s;
}

.arrow:hover {
  transform: scale(1.2);
}

/* Ïπ¥Îìú ÏòÅÏó≠ */
.work-card {
  background: #f9f6f1;
  color: #000;
  border-radius: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 40px 60px;
  max-width: 950px;
  width: 90%;
  min-height: 400px;
  opacity: 1;
  transition: opacity 0.6s ease-in-out, transform 0.4s ease-in-out;
}

/* Î∂ÄÎìúÎü¨Ïö¥ Ïä¨ÎùºÏù¥Îìú Ìö®Í≥º */
.fade {
  animation: fadeIn 0.6s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateX(30px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.laptop-wrap {
  flex: 1;
  text-align: left;
}

.laptop {
  width: 95%;
  max-width: 420px;
}

/* Ïò§Î•∏Ï™Ω ÌÖçÏä§Ìä∏ */
.work-text {
  flex: 1;
  text-align: left;
  padding-left: 40px;
  font-weight: 300;
}

.work-text h3 {
  text-align: center;
  font-size: 1.6rem;
  font-weight: 500;
  margin-bottom: 20px;
}

.work-icons {
  display: flex;
  justify-content: center;
  /* Ï§ëÏïô Ï†ïÎ†¨ ÌïµÏã¨ */
  gap: 10px;
  margin-bottom: 20px;
}

.work-icons img {
  width: 30px;
  margin-right: 8px;
}

.work-text p {
  font-size: 0.9rem;
  line-height: 1.7;
  color: #333;
  margin: 10px 0;
}

.work-meta {
  margin-top: 20px;
  font-size: 1rem;
  line-height: 1.8;
  font-weight: 300;
}

.page-list {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  margin-top: 8px;
}


.page-btn {
  background-color: #f9d74c;
  color: #222;
  font-weight: 700;
  padding: 6px 16px;
  border-radius: 50px;
  text-decoration: none;
  display: inline-block;
  transition: all 0.3s ease;
}

.page-btn:hover {
  background-color: #f4c735;
  transform: translateY(-2px);
}

/* ÌéòÏù¥ÏßÄ Î≤ÑÌäº ÎûòÌçº */
.page-tooltip-wrapper {
  position: relative;
  display: inline-block;
}

/* ÎßêÌíçÏÑ† Í∏∞Î≥∏ ÏÉÅÌÉú (Ïà®ÍπÄ) */
.tooltip {
  position: absolute;
  bottom: 120%;
  /* Î≤ÑÌäº ÏúÑÏ™ΩÏóê ÏúÑÏπò */
  left: 50%;
  transform: translateX(-50%);
  background: #fff;
  color: #222;
  font-size: 0.9rem;
  padding: 6px 10px;
  border: 2px solid #f4c735;
  border-radius: 10px;
  white-space: nowrap;
  font-weight: 600;
  opacity: 0;
  pointer-events: none;
  transition: all 0.25s ease-in-out;
  font-family: 'TheJamsil', sans-serif;
}

/* ÎßêÌíçÏÑ† ÏïÑÎûò ÌôîÏÇ¥Ìëú */
.tooltip::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  border-width: 6px;
  border-style: solid;
  border-color: #f4c735 transparent transparent transparent;
}

/* hover Ïãú ÎßêÌíçÏÑ† ÌëúÏãú */
.page-tooltip-wrapper:hover .tooltip {
  opacity: 1;
  transform: translateX(-50%) translateY(-5px);
}



/* „Ö°„Ö°„Ö°„Ö°üîπ laptop ÌîÑÎ†àÏûÑ ÏÑ∏ÌåÖ„Ö°„Ö°„Ö°„Ö° */
.laptop-frame {
  position: relative;
  display: inline-block;
}

.laptop {
  width: 100%;
  height: auto;
  display: block;
}

/* üîπ laptop.pngÏùò ÌôîÎ©¥ ÏòÅÏó≠ ÎπÑÏú® */
.laptop-screen {
  position: absolute;
  top: 14%;
  left: 16.5%;
  width: 67%;
  height: 68.5%;
  overflow: hidden;
  border-radius: 4px;
  background: #000;
}

.laptop-screen a {
  cursor: pointer;
  display: block;
  width: 100%;
  height: 100%;
}

/* üîπ ÌôîÎ©¥ Ïù¥ÎØ∏ÏßÄ */
.screen-content {
  width: 100%;
  height: auto;
  transition: transform 6s ease-in-out;
}

/* üîπ hover Ïãú Ïä§ÌÅ¨Î°§ Ìö®Í≥º */
.laptop-screen:hover .screen-content {
  transform: translateY(-80%);
}

.laptop-screen a {
  cursor: pointer;
}


/* „Ö°„Ö°„Ö°„Ö°üîπ ÏïÑÏù¥Ìè∞ ÌîÑÎ†àÏûÑ ÏÑ∏ÌåÖ „Ö°„Ö°„Ö°„Ö° */
.iphone-frame {
  position: relative;
  display: inline-block;
  width: 450px;
  /* ÌîÑÎ†àÏûÑ Ï†ÑÏ≤¥ ÌÅ¨Í∏∞ Ï°∞Ï†à */
}

.iphone {
  width: 100%;
  height: auto;
  display: block;
}

/* üîπ ÏïÑÏù¥Ìè∞ ÌôîÎ©¥ ÏúÑÏπò Ï°∞Ï†ï (Ï†ïÌôïÌûà Îì§Ïñ¥Í∞ÄÍ≤å) */
.iphone-screen {
  position: absolute;
  top: 12%;
  left: 32%;
  width: 36%;
  height: 76%;
  overflow: hidden;
  border-radius: 15px;
  background: #000;
}

.iphone-screen a {
  display: block;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

/* üîπ ÌôîÎ©¥ Ïù¥ÎØ∏ÏßÄ (Í∏∏Í≤å Ïä§ÌÅ¨Î°§ Í∞ÄÎä•) */
.iphone-screen .screen-content {
  width: 100%;
  height: auto;
  transition: transform 6s ease-in-out;
}

/* üîπ hover Ïãú Ïä§ÌÅ¨Î°§ Ìö®Í≥º */
.iphone-screen:hover .screen-content {
  transform: translateY(-82%);
}

/* „Ö°„Ö°„Ö°„Ö°üîπ ÏïÑÏù¥Ìå®Îìú ÌîÑÎ†àÏûÑ ÏÑ∏ÌåÖ „Ö°„Ö°„Ö°„Ö° */
.ipad-frame {
  position: relative;
  display: inline-block;
  width: 380px;
}

.ipad {
  width: 100%;
  height: auto;
  display: block;
}

/* ÏïÑÏù¥Ìå®Îìú ÌôîÎ©¥ ÎπÑÏú® */
.ipad-screen {
  position: absolute;
  top: 10%;
  left: 8.5%;
  width: 83%;
  height: 80%;
  overflow: hidden;
  border-radius: 3px;
  background: #000;
}

.ipad-screen a {
  display: block;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

/* ÌôîÎ©¥ Ïù¥ÎØ∏ÏßÄ */
.ipad-screen .screen-content {
  width: 100%;
  height: auto;
  transition: transform 6s ease-in-out;
}

/* hover Ïãú Ïä§ÌÅ¨Î°§ */
.ipad-screen:hover .screen-content {
  transform: translateY(-75%);
}

/* üîπ 3Í∏∞Í∏∞ ÎèôÏãú Î∞∞Ïπò (id:5Ïö©) ‚Äî ÏàòÏ†ï Î≤ÑÏ†Ñ */
.multi-device {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  margin-top: 30px;
  margin-bottom: 30px;
}

/* ÎÖ∏Ìä∏Î∂Å ‚Äî Ï§ëÏïô Î∞∞Ïπò */
.multi-device .laptop-frame {
  position: relative;
  z-index: 3;
  top: -60px;
  width: 420px;
  transform: translateY(20px);
}

/* ÏïÑÏù¥Ìå®Îìú ‚Äî ÏôºÏ™Ω Îí§ ÏÇ¥Ïßù Í≤πÏπòÍ≤å */
.multi-device .ipad-frame {
  position: absolute;
  left: 57%;
  transform: translateX(-150%) translateY(70px);
  width: 180px;
  z-index: 5;
}

/* ÏïÑÏù¥Ìè∞ ‚Äî Ïò§Î•∏Ï™Ω Ïïû Í≤πÏπòÍ≤å */
.multi-device .iphone-frame {
  position: absolute;
  left: 10%;
  transform: translateX(75%) translateY(80px);
  width: 250px;
  z-index: 4;
}

/* Î∞òÏùëÌòï Ï°∞Ï†ï */
@media (max-width: 1000px) {
  .multi-device {
    flex-direction: column;
    align-items: center;
    gap: 40px;
  }

  .multi-device .laptop-frame,
  .multi-device .ipad-frame,
  .multi-device .iphone-frame {
    position: static;
    transform: none;
    width: 70%;
    opacity: 1;
    rotate: 0deg;
  }
}


/* Î∞òÏùëÌòï */
@media (max-width: 950px) {
  .work-card {
    flex-direction: column;
    text-align: center;
    padding: 30px;
  }

  .work-text {
    padding-left: 0;
  }

  .laptop {
    width: 80%;
  }
}


/* „Ö°„Ö°„Ö°„Ö°„Ö°Contact „Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°*/
.contact-section {
  padding: 170px 0;
  color: #fff;
  display: flex;
  justify-content: center;
}

.contact-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 80px;
  width: 80%;
  max-width: 1100px;
}

/* --- ÏôºÏ™Ω ÏòÅÏó≠ --- */
.contact-left {
  flex: 1;
  text-align: center;
}

.contact-image {
  width: 680px;
  max-width: 100%;
  margin-bottom: 40px;
}

.contact-email {
  font-size: 16px;
  color: #fff;
  cursor: default;
}

.copy-icon {
  margin-left: 8px;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  transition: transform 0.2s ease;
}

.copy-icon:hover {
  transform: scale(1.2);
  color: #f4c735;
  /* Ìè¨Ìä∏Ìè¥Î¶¨Ïò§ Î©îÏù∏ Ïª¨Îü¨Î°ú Í∞ïÏ°∞ */
}


/* --- Ïò§Î•∏Ï™Ω Ìèº ÏòÅÏó≠ --- */
.contact-form {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.contact-form label {
  font-size: 1rem;
  color: #ccc;
  margin-bottom: -10px;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 10px 0;
  border: none;
  border-bottom: 1px solid #666;
  background: transparent;
  color: #fff;
  font-size: 15px;
  outline: none;
  resize: none;
}

.contact-form textarea {
  height: 100px;
}

.contact-form button {
  margin-top: 20px;
  padding: 14px;
  border-radius: 12px;
  border: none;
  background: #fff;
  color: #000;
  font-family: 'TheJamsil', 'Cafe24Supermagic', sans-serif;
  font-weight: 500;
  /* ÌïÑÏöî Ïãú Ï°∞Ï†ï */
  font-size: 1.3rem;
  cursor: pointer;
  transition: 0.3s;
}

.contact-form button:hover {
  background: #ffcc33;
}

/* --- Î∞òÏùëÌòï --- */
@media (max-width: 900px) {
  .contact-container {
    flex-direction: column;
    text-align: center;
  }

  .contact-left,
  .contact-form {
    width: 100%;
  }

  .contact-image {
    width: 300px;
  }
}


/*„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö° Footer „Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°*/
.footer {
  text-align: center;
  padding: 40px 0;
  color: #888;
}

/* Responsive */
@media (max-width: 1100px) {
  .about-grid {
    grid-template-columns: 1fr;
  }
}